l de consignaciones bancarias, haya sido superior a 3.500 UVT ($127.078.000).")]),_c('p',{staticClass:"mb-4"},[_vm._v("Su cálculo parte de una tarifa general, la cual actualmente es del 19% para la mayoría de los bienes y servicios. Sin embargo, existen tarifas diferenciales para ciertos productos. La tarifa del impuesto establece que toda empresa que venda bienes o servicios debe calcular un valor del 19% adicional al valor del producto de producción, incluida su ganancia, por concepto de este impuesto denominado IVA. Esto significa que, en la factura, la empresa, una vez totalice los valores del producto vendido al cliente, le sumará el valor correspondiente al IVA, calculando el valor total y final a pagar por parte del cliente. Este último valor es un cargo al pasivo, pues es una obligación ante el estado y no hace parte de los recursos de la empresa (IVA por pagar).")]),_c('p',{staticClass:"mb-5"},[_vm._v("Las empresas declarantes del IVA tienen dos periodos para pagar este impuesto recaudado, tal cual lo establece el Artículo 600 del Estatuto Tributario, uno bimestral y otro cuatrimestral:")]),_c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-lg-5 d-flex t2-div11 p-4 py-5 mb-4"},[_c('div',[_c('div',{staticClass:"row justify-content-start mb-5"},[_c('div',{staticClass:"col-lg-3 col-md-2 col-sm-3 col-3"},[_c('img',{staticClass:"w-75",attrs:{"src":require("@/assets/curso/tema2/img06.svg")}})])]),_c('p',{staticClass:"mb-1"},[_c('b',[_vm._v("Bimestral")])]),_c('p',{staticClass:"mb-0"},[_vm._v("Las empresas o personas naturales con ingresos brutos al 31 de diciembre del año gravable anterior, o en lo corrido del año, iguales o superiores a 92 Unidades de Valor Tributario ($3.340.336). Estas UVT son lotes monetarios que cada año se declaran y actualizan por el gobierno ($36.308 pesos para 2021).")])])]),_c('div',{staticClass:"col-lg-5 d-flex t2-div12 p-4 py-5 mb-4"},[_c('div',[_c('div',{staticClass:"row justify-content-start mb-5"},